.App {
  text-align: center;
}

.rounded {
  border-radius: 0.5rem; 
}

.text-lg {
  font-size: 1.125rem; /* 18px */
}

.font-bold {
  font-weight: bold;
}


.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.justify-between {
  justify-content: space-between;
}

.mb-2 {
  margin-bottom: 0.5rem;
}

.bg-blue-500 {
  background-color: #000000; /* Tailwind's blue-500 */
}


.text-white {
  color: #ffffff;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.px-4 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.w-12 {
  width: 3rem; 
}
 
ul{
  text-align: center;
}
.mx-4{
  text-align: justify;

}
.box{
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem; 
  max-width: 50rem;
  margin-left: auto;
  margin-right: auto;
  padding: 5rem;
}




